/* ProjectBox.css */
@import url('https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap');

/* General Styles */
.projectComponentleft {
    display: grid;
    grid-template-columns: 1.5fr 0.5fr 2fr;
    margin: auto;
    width: 70vw;
    height: auto;
    margin-bottom: 10%;
}

.projImg {
    width: 100%;
    filter: brightness(70%) grayscale(80%);
    transition: all 0.5s ease;
}

.projImg:hover {
    filter: brightness(90%) grayscale(25%);
    width: 98%;
}

.projTitle {
    font-size: calc(15px + 0.5vw);
    padding: 4%;
    width: 95%;
    font-weight: 400;
}

.projDesc {
    color: #dddddd;
    text-shadow: 0px 1px #2d3142;
    line-height: calc(15px + 0.4vw);
    font-size: calc(8px + 0.5vw);
    z-index: 5;
    padding: 5%;
    margin-left: 3%;
}

.projSkills {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
}

.projLinks {
    display: grid;
    padding-top: 4%;
    width: 10%;
}

#proj-skill {
    padding: 5px;
    font-size: 10px;
    font-weight: 500;
    margin: 2% 10% 2% 10%;
    border: 1px solid #25334b;
    border-radius: 2px;
    transition: all 0.5s ease;
}

#proj-skill:hover {
    background: rgba(22,26,43,0.25);
}

#has-git {
    grid-template-rows: 0.1fr 0.05fr auto 0.2fr 0.15fr;
}

#no-git {
    grid-template-rows: 0.1fr 0.05fr auto 0.2fr;
}

/* Left Justified Text */

#proj-img-left {
    grid-area: 1 / 2 / 6 / 4;
}

#title-left {
    grid-area: 2 / 1 / 3 / 2;
    text-align: left;
}

#desc-left {
    grid-area: 3 / 1 / 4 / 3;
    background-color: #B84C1D;
    box-shadow: -2px 4px #8D2100;
}

#proj-skills-left {
    grid-area: 4 / 1 / 5 / 2;
}

#proj-links-left {
    grid-area: 5 / 1 / 6 / 2;
    margin-left: 5%;
}

/* Right Justified Text */

.projectComponentright {
    display: grid;
    grid-template-columns: 2fr 0.5fr 1.5fr;
    margin: auto;
    width: 70vw;
    height: auto;
    margin-bottom: 10%;
}

#proj-img-right {
    grid-area: 1 / 1 / 6 / 3;
}

#title-right {
    grid-area: 2 / 3 / 3 / 4;
    text-align: right;
}

#desc-right {
    grid-area: 3 / 2 / 4 / 4;
    background-color: #4f5d75;
    box-shadow: 2px 4px #25334b;
}

#proj-skills-right {
    grid-area: 4 / 3 / 5 / 4;
}

#proj-links-right {
    grid-area: 5 / 3 / 6 / 4;
    margin-left: 90%;
}

/* Mobile Style */

.projectComponentMobile {
    display: grid;
    margin: auto;
    height: auto;
    width: 80vw;
    padding-bottom: 10%;
}

#proj-img-mobile {
    width: 80vw;
    height: 80vw;
    object-fit: cover;
    margin: auto;
    border-radius: 4px;
}

#proj-title-mobile {
    color: black;
    font-size: calc(15px + 0.5vw);
    font-weight: 600;
    z-index: 5;
    text-align: center;
    padding-bottom: 2%;
}

#proj-desc-mobile {
    color: black;
    font-size: calc(10px + 0.5vw);
    line-height: 20px;
    z-index: 5;
    padding: 0% 10% 0% 10%;
}