/* InfoBox CSS File */

.infoBox {
    position: absolute;
    top: 44px;
    width: calc(100%);
    height: calc(100%-44px);
    z-index: 5;
    border-radius: 10px;
}

#title {
  color: rgba(255,255,255,1);
  font-size: auto;
  width: 100%;
  font-weight: 300;
  text-align: center;
  z-index: 3;
  font-size: calc(18px + 0.5vw);
  margin-top: 30vh;
  text-shadow: 0px 2px #2d3142;
}

#name {
  color: rgba(255,255,255,1);
  position: relative;
  width: 100%;
  text-align: center;
  z-index: 3;
  font-size: calc(35px + 1.2vw);
  line-height: 3vh;
  text-shadow: 0px 2px #2d3142;
}

#body {
  color: rgba(255,255,255,1);
  position: relative;
  width: 100%;
  text-align: center;
  z-index: 3;
  font-size: calc(14px + 0.5vw);
  text-shadow: 0px 2px #2d3142;
}

#break {
  background-color: #4f5d75;
  position: relative;
  z-index: 3;
  width: 25vw;
  left: 37.5%;
  height: 0.4vh;
  box-shadow: #2d3142 0px 2px;
}