/* ExpBox.css */

.expComponent {
    width: 70vw;
    height: auto;
    margin: auto;
}

.expText {
    text-align: center;
}

.expList {
    display: grid;
    width: 65vw;
    margin: auto;
    padding: 0% 0 5% 0;
}

#exp-1 {
    grid-template-columns: 1fr;
}

#exp-3 {
    grid-template-columns: 1fr 1fr 1fr;
    width: 40vw;
}

#exp-photo {
    width: 120px;
    filter: brightness(75%) grayscale(80%);
    padding: 0% 2% 2% 2%;
}

#exp-photo:hover {
    width: 115px;
    filter: brightness(90%) grayscale(0%);
}
