/* Navbar Styles */
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&display=swap');

.logo {
    height: 40px;
    width: 40px;
    display: block;
    border-radius: 2px;
    z-index: 11;
    color: rgba(5,2,30,1);
    padding: 10px;
    margin: auto;
    transition: all 0.2s;
}
  
.logo:hover {
    filter: brightness(25%) opacity(50%);
    height: 38px;
    width: 38px;
}

.mobileLogo {
    height: 40px;
    width: 40px;
    display: block;
    border-radius: 2px;
    z-index: 11;
    color: rgba(5,2,30,1);
    padding: 10px;
    margin: auto;
}

.mTab {
    font-family: 'Montserrat';
    background: rgba(20,20,20,0);
    color: rgba(255,255,255,1);
    border: 0px double rgb(255,255,255);
    float: left;
    font-size: calc(8px + 1vw);
    height: 50px;
    padding-right: 10%;
    text-align: right;
    width: 90%;
    margin: auto;
    border-radius: 2px;
    z-index: 11;
    transition: font-weight 0.1s;
    cursor: pointer;
    text-shadow: 0px 2px #2d3142;
}

.mTab:hover {
    font-weight: bold;
}

.tab {
    font-family: 'Montserrat';
    font-weight: 300;
    background-color: rgba(20,20,20,0);
    color: rgba(255,255,255,1);
    border: 0px double rgb(255,255,255);
    float: left;
    font-size: calc(15px + 0.1vw);
    height: 80px;
    text-align: center;
    width: auto;
    margin: auto;
    padding: 0% 25px 0% 25px;
    z-index: 11;
    cursor: pointer;
    text-shadow: 0px 2px #2d3142;
    transition: all 0.25s ease-out;
}

.tab:hover {
    font-weight: 600;
}

.rTab {
    font-family: 'Montserrat';
    font-weight: 300;
    background-color: rgba(22,26,43,0.25);
    color: rgba(255,255,255,1);
    float: left;
    font-size: calc(15px + 0.1vw);
    height: 40px;
    text-align: center;
    width: auto;
    margin: auto;
    padding: 0% 25px 0% 25px;
    z-index: 11;
    cursor: pointer;
    text-shadow: 0px 2px #2d3142;
    transition: all 0.25s ease-out;
    border: 1px solid white;
    border-radius: 2px;
}

.rTab:hover {
    font-weight: 600;
    background-color: rgba(22,26,43,0.5);
}

#about:hover {
    background: #ef8354;
    box-shadow: 0px 4px #a63a0b
}

#edu:hover {
    background: #ef8354;
    box-shadow: 0px 4px #a63a0b
}

#port:hover {
    background: #ef8354;
    box-shadow: 0px 4px #a63a0b
}

#exp:hover {
    background: #ef8354;
    box-shadow: 0px 4px #a63a0b
}

.tabBar {
    justify-items: center;
    height: auto;
    width: 100%;
    display: grid;
    grid-template-columns: 0.5fr 0.75fr 0.75fr 1fr 1fr 1fr 1fr 0.75fr;
    position: fixed;
    top: 0%;
    left: 0%;
    z-index: 10;
}

.menu {
    display: grid;
    grid-template-rows: 60px 1fr 1fr 1fr 1fr 1fr 10fr;
    background: rgba(45,49,66,0.75);
    position: absolute;
    width: 25vw;
    height: 100vh;
    z-index: 9;
    transition: right 0.2s;
}

#menu_selected {
    right: 0vw;
}

#menu_deselected {
    right: -30vw;
}

#gap {
    grid-column-start: 2;
    grid-column-end: 4;
    width: 1vw;
}

#gap-mobile {
    grid-column-start: 2;
    grid-column-end: 8;
    width: 1vw;
}
