/* App.css - Master CSS document for Drcall portfolio website */

/* Imports */

@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&display=swap'); /* Import Font (Montserrat)*/

/* Default App Styles */

*:focus {
    outline: 0;
}

body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background: rgb(55,60,82);
}
  
code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

h1 {
    font-family: 'Montserrat';
    font-weight: 600;
}

h2 {
    font-family: 'Montserrat';
    font-weight: 300;
}

h3 {
    font-family: 'Montserrat';
    font-weight: 300;
    font-size: calc(25px + 0.6vw);
    line-height: 0px;
}

h4 {
    font-family: 'Montserrat';
    font-weight: 500;
    font-size: 17px;
    margin: auto;
    width: 100%;
}

a {
    color: #000000;
    text-decoration: none;
    transition: all 0.2s ease-out;
    border-bottom: 0px;
    font-weight: 600;
}

a:hover {
    color: #ef8354;
    border-bottom: 1px solid #ef8354;
    cursor: pointer;
}

b {
    font-family: 'Montserrat';
    font-weight: 300;
    text-align: center;
}

p {
    font-family: 'Montserrat';
    font-weight: 400;
}

img {
    height: auto;
    border-radius: 4px;
    margin: auto;
    transition: all 0.5s ease;
}

input {
    margin: auto;
}

li {
    font-family: 'Montserrat';
    font-weight: 300;
    font-size: 18px;
    padding: 0% 1% 2% 1%;
    list-style: circle;
}

/* Page Styles */

.App-header {
    position: relative;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
  }

.page {
    position: relative;
    width: 100%;
    height: auto;
    padding-bottom: 2%;
}

#page-A {
    background: #eeeeee;
    min-height: 85vh;
}

#page-B {
    background: #dddddd;
    min-height: 85vh;
}

#page-C {
    background: #eeeeee;
    min-height: 85vh;
}

#page-D {
    background: #dddddd;
    min-height: 85vh;
    display: grid;
}

#contact {
    background: rgba(37,51,75,1);
    box-shadow: 0px 4px rgb(24,38,62);
    z-index: 4;
    padding-bottom: 4%;
}

/* NON-COMPONENT STYLES */

/* Header Section */

#splash {
    position: absolute;
    width: 100%;
    height: 100vh;
    object-fit: cover;
    z-index: 1;
    transition: all 0s;
    filter: brightness(60%) blur(2px);
}
  
#down-arrow {
    position: absolute;
    z-index: 2;
    height: 25px;
    width: 30px;
    bottom: 2vh;
    transition: filter 0.1s;
}

#down-arrow:hover {
    filter: brightness(25%) opacity(50%);
}
/* Contact Styles */
  
.contactText {
    color: white;
    text-align: center;
    line-height: calc(20px + 0.5vw);
    font-size: calc(10px + 0.5vw);
    grid-template-columns: 1fr 1fr;
    width: calc(300px + 15vw);
    margin: auto;
    padding: 0% 2% 2% 2%;
}

.contactButton {
    color: white;
    text-shadow: 0px 2px #2d3142;
    font-size: 20px;
    background-color: rgba(0,0,0,0.25);
    border: 1px solid white;
    border-radius: 2px;
    padding: 5% 0% 5% 0%;
    margin: 5%;
    transition: all 0.25s ease-out;
    cursor: pointer;
}

.contactButton:hover {
    background-color: rgba(0,0,0,0.5);
    font-weight: 500;
}

.contactGrid {
    display: grid;
    margin: auto;
    width: 30vw;
}

#contact-1 {
    grid-template-columns: 1fr;
}

#contact-2 {
    grid-template-columns: 1fr 1fr;
}
  
/* Footer Styles */
.footer {
    display: grid;
    grid-template-columns: 1fr 1fr;
    background: #dddddd;
    position: relative;
    width: 100%;
    height: 5vh;
}
  
.footerContent {
    font-size: 12px;
    font-weight: 400;
    transition: all 0.25s;
    margin: auto;
}