/* AboutBox.css */

.aboutMeGrid {
    display: grid;
    grid-template-columns: 1fr 2fr 0.1fr;
    width: 70vw;
    padding: 0% 0% 7% 0%;
    margin: auto;
}

.aboutMeMobile {
    display: grid;
    grid-template-columns: 1fr;
    padding: 0% 5% 2% 5%;
}
  
#drcall {
    width: calc(200px + 10vw);
    -webkit-filter: grayscale(75%);
            filter: grayscale(75%);
}
  
#drcall:hover {
    -webkit-filter: grayscale(0%);
            filter: grayscale(0%);
    width: calc(195px + 10vw);
}
  
#about-txt-section {
    padding: 0% 4% 0% 4%;
    margin: 2%;
    height: auto;
}
  
#media-grid {
    display: grid;
    grid-template-rows: 1fr 1fr 1fr;
    width: 100%;
    height: 60%;
    margin: auto;
    border: 1px solid #25334b;
    border-radius: 2px;
    padding: 0% 5px 0% 5px;
}
  
#media-mobile {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    margin: auto;
    width: 50%;
    border: 1px solid #25334b;
    border-radius: 2px;
    padding: 5px 0px 5px 0px;
}
  
.mediaLogo {
    width: 28px;
    height: 28px;
    margin: auto;
    transition: -webkit-filter 0.25s;
    transition: filter 0.25s;
    transition: filter 0.25s, -webkit-filter 0.25s;
}

.mediaLogo:hover {
    -webkit-filter: brightness(25%) opacity(50%);
            filter: brightness(25%) opacity(50%);
    height: 25px;
    width: 25px;
}
  
.skillsGrid {
    display: grid;
    width: 80%;
    height: auto;
    margin: auto;
    vertical-align: middle;
}
  
#skill-1 {
    grid-template-columns: 1fr 1fr 1fr;
}

#skill-2 {
    grid-template-columns: 1fr 1fr;
}
  
#skill {
    margin: 10%;
    text-align: center;
    padding: 5px 0px 5px 0px;
    font-weight: 500;
    font-size: calc(9px + 0.5vw);
    border: 1px solid #25334b;
    border-radius: 2px;
    transition: all 0.5s ease;
}
  
#skill:hover {
    background: rgba(22,26,43,0.25);
}
/* ClassBox.css */

.classBox {
