/* ClassBox.css */

.classBox {
    display: grid;  
    width: 60vw;
    height: auto;
    padding: 0% 2% 0% 0%;
    margin: auto;
}

#class-reg {
    grid-template-rows: 0.5fr 0.5fr;
    grid-template-columns: 0.4fr 1fr 1fr;
}

#class-mobile {
    grid-template-columns: 1fr;
}

#school-logo {
    width: calc(60px + 1vw);
    grid-row: 1 / 3;
    padding: 5%;
    filter: brightness(50%) grayscale(80%);
}

#school-logo:hover {
    width: calc(55px + 1vw);
    filter: brightness(90%) grayscale(0%);
}

#courses {
    grid-column: 2 / 4;
    columns: 2;
}

#courses-mobile {
    grid-column: 1;
    columns: 1;
}